@import '~scss/variables';

ff-sort {
  width: 1.5rem;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: #777;
  cursor: pointer;

  &.ff-spin {
    &::before {
      z-index: 1;
    }
    .fa {
      opacity: 0;
    }
  }

  .fa {
    position: relative;
    width: 100%;
    height: 100%;
    transition: opacity .15s;
    transform: scale(1.2);

    &::before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: transform .15s;
      z-index: 1;
    }
  }

  .fa-sort-asc, .fa-sort-desc {
    color: var(--brand-color);
    font-size: 17px;
  }

  .fa-sort-asc::before {
    margin-top: 4px;
  }

  .fa-sort-desc::before {
    margin-top: -4px;
  }

  &:hover {
    .fa::before {
      transform: translate(-50%, -50%) scale(1.4);
    }
  }
}
