@import '~scss/variables';

ff-pagination {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  padding-top: .5rem;

  span {
    display: flex;
    flex: 0 0 20px;
    align-items: center;
    justify-content: center;
    margin: 4px 4px 0 0;

    cursor: pointer;
    color: $link-color;

    &:hover {
      color: var(--link-hover-color);
    }

    &.active {
      cursor: default;
      color: $text-color;
      font-weight: bold;
    }

    &.ff-spin::before {
      z-index: 1;
    }
  }
}
