ff-img {
  img {
    max-width: 100%;
    width: 100%;
  }

  &[ff-src-hq] {
    position: relative;

    .img-hq {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }

  &[img-placeholder].ff-img-placeholder { // image placeholder
    display: flex;
    flex-flow: column;

    img {
      display: none;
    }

    &::before {
      content: attr(img-placeholder);
      display: flex;
      flex: auto;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      min-height: 8rem;
      color: #bbb;
      background: #eee;
    }
  }

  &.ff-img-error {
    img {
      display: none;
    }
  }
}
