/**
 * ngCollapse directive - collapsible content block with lazy rendering
 */
.ng-collapse {
	max-height: 9999px;
	transition: max-height .3s cubic-bezier(0.5, 0, 1, 0);
}

// Collapsed block
.ng-collapsed {
	overflow: hidden;
	max-height: 0;
	transition: max-height .3s cubic-bezier(0, 1, 0, 1) -.1s;
	border: 0 !important;
}
