@import '../../scss/variables';

$ff-search-color: $text-color;
$ff-search-bg: $ff-panel-bg-color;

ff-search {
  position: relative;

  .ff-text.ff-text.ff-text {
    background-color: #fff;

    &:focus {
      position: relative;
      z-index: 2;
    }
  }
}

ff-search-options {
  position: absolute;
  width: 100%;
  max-height: 176px;
  overflow-y: scroll;
  z-index: 5;
  background: $ff-search-bg;
  border: 1px solid var(--brand-color);
  border-radius: 0;
  color: $ff-search-color;
  box-shadow: 0 2px 8px fade_out($extra-bg-color, .5);

  // ff-search-options.ffso-above is set by JS when options dropdown doesn't fit to the bottom of screen
  &.ffso-above {
    bottom: 100%;
  }

  .ff-search-empty {
    padding: 3px 5px;
  }

  .ff-search-option {
    border-bottom: 1px solid #e7e8e9;
    cursor: pointer;

    &:hover {
      background: darken($ff-search-bg, 2);
    }

    div {
      width: 100%;

      h6 {
        padding: 2px 5px 1px;
      }

      small {
        padding: 0 5px 2px;
        display: block;
      }
    }

    &.active {
      div {
        background: var(--brand-color);
      }

      h6, small {
        background: rgba(255, 255, 255, .75);
      }
    }

    &:last-of-type {
      border-bottom: 0;
    }
  }

  // search field matches in ffSearchOptions
  .match {
    font-style: italic;

    span {
      font-style: normal;
    }
  }
}

ff-search-match {
  position: absolute;
  top: 1px;
  bottom: 1px;
  left: 1px;
  right: 1px;
  padding: 0 $ff-control-padding;
  overflow: hidden;
  background: $ff-search-bg;

  &.active {
    z-index: 3;
  }
}

ff-search-match, .ff-search-option {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  line-height: 1.2;

  h6 {
    font-size: 1em;
    font-weight: normal;
    margin: 0;
    padding: 0;
    color: lighten($ff-search-color, 20%);
    flex: 0 0 100%;

    span {
      font-size: 0.9em;

      &:first-of-type {
        color: $ff-search-color;
        font-size: 1em;
      }
    }
  }
}
