@import '~scss/variables';

ff-textarea {
  position: relative; // for absolutely positioned .ng-validate-alert
  min-height: $ff-control-min-height * 2.1; // enough space for 4 lines without vertical scroll

  .ff-control {
    padding: $ff-control-padding; // full padding for textarea
  }

  .ff-static { // readonly textarea
    white-space: pre-wrap;
    align-items: flex-start;
  }
}
