$btn-senses: 'delete', 'remove', 'new', 'add', 'save', 'edit', 'upload', 'clone', 'import', 'charge', 'makeFavorite', 'next', 'collapseBtnPriv';

@mixin display($display) {
  @each $sense in $btn-senses {
    ff-btn[sense=#{$sense}] {
      display: $display;
    }
  }
}

.ff-bulk-read-only {
  @include display(none);

  ff-btn[sense=view].hidden {
    display: inherit !important;
  }

  .ff-read-only-ignore {
    @include display(inherit);

    ff-btn[sense=view].hidden {
      display: none !important;
    }
  }
}
