@import '~scss/variables';

.ff-tip {
  position: absolute;
  top: -4px;
  left: 50%;
  min-width: 160px;
  padding: 5px;
  z-index: 150;

  font-size: .9rem;
  color: var(--brand-color);
  background-color: currentColor;
  border: 1px solid #fff;
  border-radius: calc(var(--panel-border-radius) / 2);
  box-shadow: -1px 1px 3px rgba(0,0,0, 0.3);

  text-transform: none;
  text-align: left;

  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0);
  transition: opacity .15s ease, transform .15s ease;

  & > i { // Triangle
    position: absolute;
    display: block;
    width: 14px;
    height: 14px;
    left: 50%;
    bottom: -7px;
    transform: translateX(-50%);
    color: inherit;

    &::after, &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0;
      height: 0;
      border: 1px solid transparent;
      pointer-events: none;
    }
    &::after {
      left: 1px;
      bottom: 1px;
      border-width: 6px 6px 0 6px;
      border-top-color: currentColor;
    }
    &::before {
      border-width: 7px 7px 0 7px;
      border-top-color: #fff;
    }
  }

  &.rc-marker-info {
    width: max-content;
    max-width: 220px;
  }

  .ff-tip-trigger:hover & {
    opacity: 1;
    transform: translate3d(-50%, -100%, 0) scale(1);

    &.ff-tip-bottom {
      transform: translate3d(-50%, 100%, 0) scale(1);
    }
  }

  .ff-tip-inner { // inner tip container for content styling
    color: var(--text-on-brand-color);
  }

  h6 {
    margin: 0;
    text-transform: uppercase;
    font-weight: inherit;
  }

  a {
    color: var(--text-on-brand-color) !important;
    text-decoration: underline;
  }

  &.default {
    color: var(--gray-bg-color);
    .ff-tip-inner {
      color: #fff;
    }
  }
  &.primary {
    color: var(--black-bg-color);
    .ff-tip-inner {
      color: #fff;
    }
  }
  &.info {
    color: var(--brand-color);
  }
  &.warning, &.danger {
    color: var(--danger-color);
    .ff-tip-inner {
      color: #fff;
    }
  }
  &.white {
    color: #fff;
    border-color: var(--gray-bg-color);

    & > i::before {
      border-top-color: var(--gray-bg-color);
    }

    .ff-tip-inner {
      color: $text-color;
    }

    &.ff-tip-right i::before {
      border-right-color: var(--gray-bg-color);
    }
  }
}

// bottom tip
.ff-tip-bottom {
  top: auto;
  bottom: -4px;
  transform: translate3d(-50%, 0, 0) scale(0);
  box-shadow: -1px -1px 3px rgba(0,0,0, 0.3);

  & > i { // Triangle
    top: -7px;
    bottom: auto;

    &::after, &::before {
      top: 0;
      bottom: auto;
    }
    &::before {
      border-width: 0 7px 7px 7px;
      border-top-color: transparent;
      border-bottom-color: #fff;
    }
    &::after {
      border-width: 0 6px 6px 6px;
      border-top-color: transparent;
      border-bottom-color: currentColor;
      top: 1px;
    }
  }
}

// right tip
.ff-tip-right {
  top: 50%;
  left: 100%;
  margin-left: 0;
  transform: translate3d(0, -50%, 0);
  box-shadow: 1px 1px 3px rgba(0,0,0, 0.3);
  display: none;

  & > i { // Triangle
    left: -7px;
    top: 50%;
    bottom: auto;
    transform: translateY(-50%);

    &::before {
      border-width: 7px 7px 7px 0;
      border-top-color: transparent !important;
      border-right-color: #fff;
    }
    &::after {
      border-width: 6px 6px 6px 0;
      border-top-color: transparent !important;;
      border-right-color: currentColor;
      left: 1px;
    }
  }

  .ff-tip-trigger:hover & {
    display: block;
    transform: translate3d(0, -50%, 0);
  }
}

.ff-hint {
  position: relative;
  display: inline-block;
  width: 15px;
  height: 18px;
  padding: 0;
  line-height: 18px;
  text-align: right;

  .ff-icon {
    color: var(--brand-color);
    font-size: 1rem;

    &.sense-warning, &.sense-danger {
      color: var(--danger-color);
    }

    &:not(.sense-danger):not(.sense-warning) { // inverse icon
      &::before { // icon
        position: relative;
        z-index: 1;
      }
      &::after { // background
        content: '';
        display: block;
        width: 6px;
        height: 11px;
        margin-left: 1px;
        margin-top: -1px;
        background-color: var(--text-on-brand-color);
        border-radius: 30%;
        position: absolute;
        top: 50%;
        transform: translate(-50%,-50%);
        left: 50%;
      }
    }
  }
}
